import { useContext } from "react";
import { GlobalContext } from "../context/GlobalContext";

const IncomeExpenses = () => {

  const {transactions} = useContext(GlobalContext)

  const Money = transactions.map( e => e.amount)

  const Incomes = Money.filter( e => e > 0).reduce( (a,b) => a += b, 0)

  const Expenses = Math.abs(Money.filter( e => e < 0).reduce( (a,b) => a += b, 0))

  return (
    <div className="comp" id='InEx'>
      <div className="in">
        Incomes
        <span className="gain">${Incomes}</span>
      </div>
      <div className="ex">
        Expenses
        <span className="pain">${Expenses}</span>
      </div>
    </div>
  );
}
 
export default IncomeExpenses;