import React, {createContext, useEffect, useReducer, useState} from 'react';
import AddReducer from './AddReducer';
import { projectFirestore } from '../firebase/config';

const initialState = {
  transactions :[
    {id:1, name:"Salary", amount:600},
    {id:2, name:"Phone", amount:-200},
    {id:3, name:"Pizza", amount:-20},
    {id:4, name:"Picnic", amount:-50}
  ]
}

export const GlobalContext = createContext()

export const GlobalProvider = ({children}) => {

  const [ data, setData ] = useState({transactions:[]})

  useEffect( () => {
    projectFirestore.collection("expenses")
      .onSnapshot( snap => {
        if (snap.empty) {
          console.log("empty snap");
        }
        let documents = []
        snap.forEach( doc => {
          documents.push({...doc.data(), id: doc.id})
          console.log(doc.data());
        });
        console.log(documents);
        setData({transactions:documents})
      })
    }, [])

  console.log(data);

  // const [state, dispatch] = useReducer(AddReducer, data)
  const [state, dispatch] = useReducer(AddReducer, data)

  console.log(state);

  const deleteTransaction = (id) =>{
    dispatch({
      type:"DELETE_TRANSACTION",
      payload:id
    })
  }

  const addTransaction = (transaction) =>{
    dispatch({
      type:"ADD_TRANSACTION",
      payload:transaction
    })
  }

  return (
    <GlobalContext.Provider 
      value={{
        transactions : data.transactions,
        deleteTransaction,
        addTransaction
      }}
    >
      {children}
    </GlobalContext.Provider>
  );
}
