import { useContext } from "react";
import { GlobalContext } from "../context/GlobalContext";

const Transaction = ({transaction}) => {

  const {deleteTransaction} = useContext(GlobalContext);
  return (
    <li className={ (transaction.amount > 0 ? "more" : " less") + " trans" }>
      <span id="name">{transaction.name}</span>
      <span id="amount">{Math.abs(transaction.amount)}</span>
      <span id="delete-btn" onClick={ () => deleteTransaction(transaction.id)}>X</span>
    </li>
  );
}

export default Transaction;