import { useContext } from "react";
import { GlobalContext } from "../context/GlobalContext";

const Balance = () => {

  const {transactions} = useContext(GlobalContext)

  const money = transactions.map( e => e.amount)

  const balance = money.reduce( (prev, curnt) => prev += curnt, 0)

  return (
    <div className="comp">
      <h4>Your Balance</h4>
      {/* <h1>$0.00</h1> */}
      <h1>${balance}</h1>
    </div>
  );
}
 
export default Balance;