import { useContext, useState } from "react"
import { GlobalContext } from "../context/GlobalContext";
// import { UserContext } from "../App";


const AddTransaction = () => {

  const [ name, setName ] =useState('');
  const [ money, setMoney ] = useState(0);

  const {addTransaction} = useContext(GlobalContext);

  // const transaction = { name, amount: +money, id: Math.random()*10000 }
  const transaction = { name, amount: +money, fakeId: Math.random()*10000}

  return (
    <div className="comp add-trans">
      <h3>Add New Transaction</h3>
      <h4>Name</h4>
      <input value={name} onChange={ e => setName(e.target.value)} placeholder="Enter name of the transaction"/>
      <h4>Money</h4>
      <input value={money} onChange={ e => setMoney(e.target.value)} />
      <button type="submit" className="btn" onClick={() => addTransaction(transaction)}>Add</button>
      {/* <h2>{user}</h2> */}
    </div>
  );
}
 
export default AddTransaction;